'use client';

import { useEffect, useState } from 'react';
import { useParams } from 'next/navigation';
import Link from 'next/link';
import { poiData, POI } from '@/lib/poiData';
import POIDetails from '@/components/POIDetails';
import FeedbackForm from '@/components/FeedbackForm';
import { Button } from '@/components/ui/button';
import { Alert, AlertDescription } from '@/components/ui/alert';

interface UserLocation {
  latitude: number;
  longitude: number;
}

export default function POIPage() {
  const params = useParams();
  const id = params.id as string;
  
  const [poi, setPoi] = useState<POI | null>(null);
  const [userLocation, setUserLocation] = useState<UserLocation | null>(null);
  const [distance, setDistance] = useState<number | undefined>(undefined);
  const [isLoading, setIsLoading] = useState(true);

  useEffect(() => {
    // Find the POI by ID
    const foundPoi = poiData.find(p => p.id === id);
    setPoi(foundPoi || null);
    setIsLoading(false);

    // Get user location for distance calculation
    if (foundPoi && navigator.geolocation) {
      navigator.geolocation.getCurrentPosition(
        (position) => {
          const userLoc = {
            latitude: position.coords.latitude,
            longitude: position.coords.longitude,
          };
          setUserLocation(userLoc);
          
          // Calculate distance
          const calculatedDistance = calculateDistance(
            userLoc.latitude,
            userLoc.longitude,
            foundPoi.latitude,
            foundPoi.longitude
          );
          setDistance(calculatedDistance);
        },
        (error) => {
          console.error('Geolocation error:', error);
          // Continue without location data
        }
      );
    }
  }, [id]);

  const calculateDistance = (lat1: number, lon1: number, lat2: number, lon2: number): number => {
    const R = 6371; // Radius of the Earth in kilometers
    const dLat = (lat2 - lat1) * Math.PI / 180;
    const dLon = (lon2 - lon1) * Math.PI / 180;
    const a = 
      Math.sin(dLat/2) * Math.sin(dLat/2) +
      Math.cos(lat1 * Math.PI / 180) * Math.cos(lat2 * Math.PI / 180) * 
      Math.sin(dLon/2) * Math.sin(dLon/2);
    const c = 2 * Math.atan2(Math.sqrt(a), Math.sqrt(1-a));
    return R * c;
  };

  if (isLoading) {
    return (
      <div className="min-h-screen flex items-center justify-center">
        <div className="text-center">
          <div className="w-8 h-8 border-4 border-blue-600 border-t-transparent rounded-full animate-spin mx-auto mb-4"></div>
          <p className="text-gray-600">Loading heritage site details...</p>
        </div>
      </div>
    );
  }

  if (!poi) {
    return (
      <div className="min-h-screen bg-gray-50">
        <div className="max-w-4xl mx-auto px-4 sm:px-6 lg:px-8 py-16">
          <Alert className="mb-8">
            <AlertDescription>
              Heritage site not found. The requested location may not exist or may have been moved.
            </AlertDescription>
          </Alert>
          
          <div className="text-center">
            <h1 className="text-3xl font-bold text-gray-900 mb-4">
              Heritage Site Not Found
            </h1>
            <p className="text-gray-600 mb-8">
              We couldn't find the heritage site you're looking for. 
              It may have been removed or the link may be incorrect.
            </p>
            
            <div className="flex flex-col sm:flex-row gap-4 justify-center">
              <Link href="/map">
                <Button size="lg">
                  Browse All Sites
                </Button>
              </Link>
              <Link href="/">
                <Button variant="outline" size="lg">
                  Back to Home
                </Button>
              </Link>
            </div>
          </div>
        </div>
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-gray-50">
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
        {/* Navigation Breadcrumb */}
        <nav className="mb-8">
          <div className="flex items-center space-x-2 text-sm text-gray-600">
            <Link href="/" className="hover:text-gray-900">Home</Link>
            <span>/</span>
            <Link href="/map" className="hover:text-gray-900">Heritage Map</Link>
            <span>/</span>
            <span className="text-gray-900 font-medium">{poi.title}</span>
          </div>
        </nav>

        {/* POI Details */}
        <div className="mb-12">
          <POIDetails poi={poi} distance={distance} />
        </div>

        {/* Action Buttons */}
        <div className="mb-12">
          <div className="bg-white rounded-lg shadow-lg p-6">
            <h2 className="text-2xl font-bold text-gray-900 mb-4">
              Explore This Site
            </h2>
            <p className="text-gray-700 mb-6">
              Enhance your visit with our interactive features and navigation tools.
            </p>
            <div className="grid sm:grid-cols-2 lg:grid-cols-4 gap-4">
              <Link href="/ar">
                <Button className="w-full h-auto p-4 flex flex-col items-center">
                  <div className="w-8 h-8 bg-blue-100 rounded-full mb-2 flex items-center justify-center">
                    <div className="w-4 h-4 bg-blue-600 rounded"></div>
                  </div>
                  <span className="font-semibold">AR Experience</span>
                  <span className="text-xs text-center mt-1">View in augmented reality</span>
                </Button>
              </Link>
              
              <Link href="/map">
                <Button variant="outline" className="w-full h-auto p-4 flex flex-col items-center">
                  <div className="w-8 h-8 bg-green-100 rounded-full mb-2 flex items-center justify-center">
                    <div className="w-4 h-4 bg-green-600 rounded-full"></div>
                  </div>
                  <span className="font-semibold">Navigate</span>
                  <span className="text-xs text-center mt-1">Get directions on map</span>
                </Button>
              </Link>

              <Button variant="outline" className="w-full h-auto p-4 flex flex-col items-center">
                <div className="w-8 h-8 bg-purple-100 rounded-full mb-2 flex items-center justify-center">
                  <div className="w-4 h-4 bg-purple-600 rounded"></div>
                </div>
                <span className="font-semibold">Audio Guide</span>
                <span className="text-xs text-center mt-1">Listen to narration</span>
              </Button>

              <Button variant="outline" className="w-full h-auto p-4 flex flex-col items-center">
                <div className="w-8 h-8 bg-orange-100 rounded-full mb-2 flex items-center justify-center">
                  <div className="w-4 h-4 bg-orange-600 rounded"></div>
                </div>
                <span className="font-semibold">Share</span>
                <span className="text-xs text-center mt-1">Share this location</span>
              </Button>
            </div>
          </div>
        </div>

        {/* Feedback Form */}
        <div className="mb-8">
          <FeedbackForm poiTitle={poi.title} poiId={poi.id} />
        </div>

        {/* Related Sites */}
        <div className="bg-white rounded-lg shadow-lg p-6">
          <h2 className="text-2xl font-bold text-gray-900 mb-6">
            Other Heritage Sites
          </h2>
          <div className="grid md:grid-cols-2 lg:grid-cols-3 gap-4">
            {poiData
              .filter(p => p.id !== poi.id)
              .slice(0, 3)
              .map((relatedPoi) => {
                const relatedDistance = userLocation 
                  ? calculateDistance(
                      userLocation.latitude, 
                      userLocation.longitude, 
                      relatedPoi.latitude, 
                      relatedPoi.longitude
                    )
                  : undefined;

                return (
                  <Link key={relatedPoi.id} href={`/poi/${relatedPoi.id}`}>
                    <div className="border border-gray-200 rounded-lg p-4 hover:shadow-md transition-shadow cursor-pointer">
                      <h3 className="font-semibold text-gray-900 mb-2">
                        {relatedPoi.title}
                      </h3>
                      <p className="text-gray-600 text-sm mb-2 line-clamp-2">
                        {relatedPoi.description}
                      </p>
                      {relatedDistance && (
                        <p className="text-blue-600 text-sm font-medium">
                          {relatedDistance.toFixed(1)} km away
                        </p>
                      )}
                    </div>
                  </Link>
                );
              })}
          </div>
        </div>
      </div>
    </div>
  );
}
