'use client';

import { useState } from 'react';
import { Button } from '@/components/ui/button';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card';
import { Textarea } from '@/components/ui/textarea';
import { Label } from '@/components/ui/label';
import { RadioGroup, RadioGroupItem } from '@/components/ui/radio-group';

interface FeedbackFormProps {
  poiTitle: string;
  poiId: string;
}

export default function FeedbackForm({ poiTitle, poiId }: FeedbackFormProps) {
  const [rating, setRating] = useState<string>('');
  const [experience, setExperience] = useState<string>('');
  const [comments, setComments] = useState<string>('');
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [isSubmitted, setIsSubmitted] = useState(false);

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    
    if (!rating || !experience) {
      alert('Please provide both a rating and experience type.');
      return;
    }

    setIsSubmitting(true);

    // Simulate API call
    try {
      await new Promise(resolve => setTimeout(resolve, 1000));
      
      // Log feedback (in a real app, this would be sent to a server)
      console.log('Feedback submitted:', {
        poiId,
        poiTitle,
        rating,
        experience,
        comments,
        timestamp: new Date().toISOString()
      });

      setIsSubmitted(true);
    } catch (error) {
      console.error('Error submitting feedback:', error);
      alert('Failed to submit feedback. Please try again.');
    } finally {
      setIsSubmitting(false);
    }
  };

  const resetForm = () => {
    setRating('');
    setExperience('');
    setComments('');
    setIsSubmitted(false);
  };

  if (isSubmitted) {
    return (
      <Card className="bg-green-50 border-green-200">
        <CardHeader>
          <CardTitle className="text-green-800">Thank You!</CardTitle>
          <CardDescription className="text-green-700">
            Your feedback has been submitted successfully.
          </CardDescription>
        </CardHeader>
        <CardContent>
          <p className="text-green-700 mb-4">
            We appreciate your input about {poiTitle}. Your feedback helps us improve 
            the heritage tourism experience for all visitors.
          </p>
          <Button onClick={resetForm} variant="outline" className="border-green-300 text-green-700 hover:bg-green-100">
            Submit Another Review
          </Button>
        </CardContent>
      </Card>
    );
  }

  return (
    <Card>
      <CardHeader>
        <CardTitle>Share Your Experience</CardTitle>
        <CardDescription>
          Help other visitors by sharing your thoughts about {poiTitle}
        </CardDescription>
      </CardHeader>
      <CardContent>
        <form onSubmit={handleSubmit} className="space-y-6">
          {/* Overall Rating */}
          <div className="space-y-3">
            <Label className="text-base font-semibold">Overall Rating</Label>
            <RadioGroup value={rating} onValueChange={setRating}>
              <div className="flex items-center space-x-2">
                <RadioGroupItem value="excellent" id="excellent" />
                <Label htmlFor="excellent">Excellent - Exceeded expectations</Label>
              </div>
              <div className="flex items-center space-x-2">
                <RadioGroupItem value="good" id="good" />
                <Label htmlFor="good">Good - Met expectations</Label>
              </div>
              <div className="flex items-center space-x-2">
                <RadioGroupItem value="average" id="average" />
                <Label htmlFor="average">Average - Satisfactory experience</Label>
              </div>
              <div className="flex items-center space-x-2">
                <RadioGroupItem value="poor" id="poor" />
                <Label htmlFor="poor">Poor - Below expectations</Label>
              </div>
            </RadioGroup>
          </div>

          {/* Experience Type */}
          <div className="space-y-3">
            <Label className="text-base font-semibold">What type of experience did you have?</Label>
            <RadioGroup value={experience} onValueChange={setExperience}>
              <div className="flex items-center space-x-2">
                <RadioGroupItem value="ar-experience" id="ar-experience" />
                <Label htmlFor="ar-experience">AR Experience - Used augmented reality features</Label>
              </div>
              <div className="flex items-center space-x-2">
                <RadioGroupItem value="map-navigation" id="map-navigation" />
                <Label htmlFor="map-navigation">Map Navigation - Used map to find location</Label>
              </div>
              <div className="flex items-center space-x-2">
                <RadioGroupItem value="educational" id="educational" />
                <Label htmlFor="educational">Educational - Focused on learning about history</Label>
              </div>
              <div className="flex items-center space-x-2">
                <RadioGroupItem value="tourism" id="tourism" />
                <Label htmlFor="tourism">Tourism - General sightseeing visit</Label>
              </div>
            </RadioGroup>
          </div>

          {/* Comments */}
          <div className="space-y-3">
            <Label htmlFor="comments" className="text-base font-semibold">
              Additional Comments (Optional)
            </Label>
            <Textarea
              id="comments"
              placeholder="Share more details about your visit, suggestions for improvement, or what you enjoyed most..."
              value={comments}
              onChange={(e) => setComments(e.target.value)}
              rows={4}
            />
          </div>

          {/* Submit Button */}
          <Button 
            type="submit" 
            disabled={isSubmitting || !rating || !experience}
            className="w-full"
          >
            {isSubmitting ? 'Submitting...' : 'Submit Feedback'}
          </Button>
        </form>

        {/* Feedback Benefits */}
        <div className="mt-6 p-4 bg-blue-50 rounded-lg">
          <h4 className="font-semibold text-blue-900 mb-2">Why Your Feedback Matters</h4>
          <ul className="text-blue-800 text-sm space-y-1">
            <li>• Helps improve AR and navigation features</li>
            <li>• Guides development of educational content</li>
            <li>• Assists other visitors in planning their trips</li>
            <li>• Contributes to heritage site preservation efforts</li>
          </ul>
        </div>
      </CardContent>
    </Card>
  );
}
